body {
  margin: 0;
  padding: 0;
  font-family: 'Source Sans Pro', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fdfcfd;
  color: #ffffff;
}

.parallax {
  background-image: url(/winfo2018/static/media/background.43a7370a.jpeg);
  min-height: 100vh;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.parallax-title {
  font-family: 'Press Start 2P', cursive;
  font-size: 5em;
}

.blink {
  animation: blink 1s;
  animation-iteration-count: infinite;
  -webkit-animation: blink 1s;
  -webkit-animation-iteration-count: infinite;
}

.fish-logo {
  padding-top: 40vh;
  width: 15%;
}

/* Navigation */
.nav-container {
  width: 100%;
  position: fixed;
  top: 0;
}

.nav-link {
  color: #fdfcfd;
  font-size: 0.75em;
  font-family: 'Press Start 2P', cursive;
}

.nav-container-colored {
  width: 100%;
  height: 70px;
  position: fixed;
  top: 0;
  background-image: -webkit-linear-gradient(rgb(248, 180, 250), rgb(191, 148, 227), rgb(152, 148, 227));
  background-image: linear-gradient(rgb(248, 180, 250), rgb(191, 148, 227), rgb(152, 148, 227));
  -webkit-transition: background-image 200ms linear;
  transition: background-image 200ms linear; 
  z-index: 100;
}

.nav-item a:hover {
  border-bottom: 5px solid currentColor;
  color: #ffffff;
}

.fish-icon {
  padding: 7px;
  float:left;
  width: 4%;
}

/* About Splash */
.splash-container {
  height: 350px;
}

.about-header {
  color: #a89ed4;
}

.about-text {
  color: #6a577c;
}

/* Carousel */
.carousel {
  position: relative;
}

.button {
  border-radius: 2em;
  background-color: #d1b1df;
  border: none;
  color: #FFFFFF;
  text-align: center;
  font-size: 1.5em;
  padding: 0.5em;
  width: 12em;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  cursor: pointer;
  box-shadow: 2px 2px 5px #cccccc;
}

.button span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.button span:after {
  content: '\BB';
  position: absolute;
  opacity: 0;
  top: 0;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.button:hover span {
  padding-right: 0.75em;
}

.button:hover span:after {
  opacity: 1;
  right: 0;
}

.no-color {
  color: #fff;
}

/* Footer */
.footer-logo {
  width: 2.5%;
}

.footer-text {
  background: -webkit-linear-gradient(rgb(248, 180, 250), rgb(191, 148, 227));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

/* Donate */
.coin {
  width: 10%;
}

.donate-spacer {
  font-family: 'Press Start 2P', cursive;
  color: #a89ed4;
}

.donate-text {
  color: #a89ed4;
  font-size: 1.20em;
}

.donate-link {
  color: #a89ed4;
  font-size: 1.20em;
}

/* Animation */
@-webkit-keyframes blink {
  0% {
      opacity: 1;
  }
  50% {
      opacity: 0;
  }
  100% {
      opacity: 1;
  }
}
@keyframes blink {
  0% {
      opacity: 1;
  }
  50% {
      opacity: 0;
  }
  100% {
      opacity: 1;
  }
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin
}

